import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, PopoverController, List } from 'ionic-angular';
import { OrderFoodProvider, CFood, COrder, COrderDetails } from '../../providers/order-food/order-food';
import * as math from 'mathjs'
/**
 * Generated class for the OrderPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-order',
  templateUrl: 'order.html',
})
export class OrderPage {
  inputText: String[] = [];
  itemsFood: any[];
  currentTable: string = "";

  //
  expression: any = "";
  backValue: any = "";
  result: any;
  //
  constructor(private navCtrl: NavController,
    private navParams: NavParams,
    private orderProvider: OrderFoodProvider,
    private popoverCtrl: PopoverController,
    private alertCtrl: AlertController) {
    this.itemsFood = this.orderProvider.listDataFood;
  }

  getItems(ev: any) {

    let val = ev.target.value;
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      val = this.xoaDau(val)
      this.itemsFood = this.orderProvider.listDataFood.filter((item) => {
        return (this.xoaDau(item.name).toLowerCase().includes(val.toLowerCase()) == true);
      })
    }
    else {
      this.itemsFood = this.orderProvider.listDataFood;
    }
  }

  addFood(food: CFood) {
    let indexFood = -1;
    let orderDetail = new COrderDetails();
    orderDetail.id = food.id;
    orderDetail.sl = 1;
    orderDetail.status = false;
    orderDetail.food.copy(food);
    for (let index = 0; index < this.orderProvider.orderTable.listOrder.length; index++) {
      if (this.orderProvider.orderTable.listOrder[index].id == food.id) {
        indexFood = index;
        break;
      }
    }
    this.orderProvider.orderTable.listOrder.push(orderDetail);
    if (indexFood != -1) {
      this.orderProvider.listSLFoodObject[food.id] = this.orderProvider.listSLFoodObject[food.id] + 1;
    }
    else {
      this.orderProvider.listSLFoodObject[food.id] = 1;
    }
  }

  minusFood(food: CFood) {
    let indexFood = -1;
    for (let index = 0; index < this.orderProvider.orderTable.listOrder.length; index++) {
      if (this.orderProvider.orderTable.listOrder[index].id == food.id) {
        indexFood = index;
        break;
      }
    }
    if (indexFood > -1) {
      this.orderProvider.orderTable.listOrder.splice(indexFood, 1);
      if (this.orderProvider.listSLFoodObject[food.id] == 1) {
        this.orderProvider.listSLFoodObject[food.id] = null;
      }
      else if (indexFood != -1 && this.orderProvider.listSLFoodObject[food.id] > 1) {
        this.orderProvider.listSLFoodObject[food.id] = this.orderProvider.listSLFoodObject[food.id] - 1;
      }
    }
  }

  xoaDau(str) {
    str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
    str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
    str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
    str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
    str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
    str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
    str = str.replace(/đ/g, "d");
    str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
    str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
    str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
    str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
    str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
    str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
    str = str.replace(/Đ/g, "D");
    return str;
  }

  ///////////start
  add(value: string) {
    let check = this.checkExpression(value);
    if (check) {
      this.backValue = value;
      console.log('CalculatorScientificPage::add | value=', value);

      const noexpression = (this.expression === '' || this.expression === undefined) ? true : false;

      if (this.expression === '' || this.expression === undefined) {
        this.expression = String(value);
      } else {
        console.log('CalculatorScientificPage::add | add value ', value);
        // check 2 number
        let num1 = this.expression.trim();
        let num2 = value.trim();
        let test1 = /^\d+$/.test(num1);
        let test2 = /^\d+$/.test(num2);

        if (test1 === true && test2 === true) {
          this.expression = this.expression.concat(value);
        } else {
          this.expression = this.expression.concat(" " + value);
        }
      }

      console.log('CalculatorScientificPage::add | expression=', this.expression);
    } else {
      let alert = this.alertCtrl.create({
        title: 'WRONG',
        subTitle: 'TRY AGAIN',
        buttons: ['OK']
      });
      alert.present();
    }

  }

  clean(data) {
    for (let i = 0; i < data.length; i++) {
      if (data[i] === '') {
        data.splice(i, 1);
      }
    }
    return data;
  }

  clear() {
    this.expression = '';
    console.log('CalculatorScientificPage::clear | ', this.expression);
  }

  addToArray() {
    //
    if (this.expression.trim() != '') {
      this.inputText.push(this.expression);
      this.expression = '';
    }
    //
  }

  undo() {
    let string = this.expression;
    let arr = string.split(" ");
    arr.pop();
    let value = "";
    for (let i = 0; i < arr.length; i += 1) {
      value = value + " " + arr[i];
    }
    this.expression = value;
  }

  checkExpression(value) {
    let string = this.expression;
    let arr = string.split(" ");
    let bol = false;
    let a = arr[0];
    let test = /^\d+$/.test(arr[0]);

    if (string === "") {
      let test = /^\d+$/.test(value);
      if (test) {
        return true;
      } else {
        return false;
      }
    } else {
      if (arr.length >= 2) {
        return true;
      }
      let thisExpressions = [/^\d+$/, /Nhỏ/, /Lớn/, /ĐầyĐủ/, /ĐặcBiệt/, /Cua/, /Tôm/, /Giò/, /Thịt/, /Nạc/,
        /Gân/, /Móng/, /Bánh/, /Huyết/, /Nấm/];
      let bol = this.matchInArray(value, thisExpressions);
      if (bol) {
        return true;
      } else {
        return false;
      }
    }
  }

  matchInArray(string, expressions) {
    let len = expressions.length,
      i = 0;
    for (; i < len; i++) {
      if (string.match(expressions[i])) {
        return true;
      }
    }
    return false;
  };

  addOrder(data: string, ev: any) {
    //
    this.itemsFood = this.orderProvider.listDataFood;
    this.itemsFood;
    let orderDetail = new COrderDetails();
    orderDetail.id = '1548948325490';
    orderDetail.sl = 1;
    orderDetail.status = false;
    orderDetail.food.copy(this.itemsFood[0]);
    this.orderProvider.orderTable.listOrder.push(orderDetail);
    //

    if (!data || this.orderProvider.orderTable.listOrder.length == 0) {
      return;
    }
    if (data == this.orderProvider.stringModeMuaVe) {
      const prompt = this.alertCtrl.create({
        title: 'Tên người mua',
        // inputs: [
        //   {
        //     name: 'tableNumber',
        //     placeholder: 'Nhập tên'
        //   },
        // ],
        buttons: [
          {
            text: 'Đóng',
            handler: data => {
            }
          },
          {
            text: 'OK',
            handler: data => {
              //
              let str = '';
              this.inputText.forEach(element => {
              str = str + element + '@';
              //
              });
              // this.currentTable = data.tableNumber.toLowerCase();
              let popover = this.popoverCtrl.create("OrderDetailsPage", {
                mode: this.orderProvider.stringModeMuaVe,
                currentTable: str
              }, {
                  cssClass: 'custom-popover'
                });
              popover.present({
                ev: ev
              });
            }
          }
        ],
        cssClass: 'customCSS'
      });
      prompt.present();
    }
    else if (data == this.orderProvider.stringModeTaiBan) {
      const prompt = this.alertCtrl.create({
        title: 'Chọn bàn',
        inputs: [
          {
            name: 'tableNumber',
            placeholder: 'Nhập số bàn',
            value: '123'
          },
        ],
        buttons: [
          {
            text: 'Đóng',
            handler: data => {
            }
          },
          {
            text: 'OK',
            handler: data => {
              // if (data.tableNumber) {
                //
                let str = '';
                this.inputText.forEach(element => {
                str += element + '@';
                //
                });
                str += data.tableNumber;
                // this.currentTable = data.tableNumber.toLowerCase();
                // this.currentTable = data.tableNumber;
                let popover = this.popoverCtrl.create("OrderDetailsPage", {
                  mode: this.orderProvider.stringModeTaiBan,
                  // currentTable: this.currentTable
                  currentTable: str
                }, {
                    cssClass: 'custom-popover'
                  });
                popover.present({
                  ev: ev
                });
              // }
              // else {
              //   return false
              // }
            }
          }
        ],
        cssClass: 'customCSS'
      });
      prompt.present();
    }
  }

  ///////////end
}



